import { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import Stack from '@mui/material/Stack';
import DoneTwoToneIcon from '@mui/icons-material/DoneTwoTone';
import ClearIcon from '@mui/icons-material/Clear';
import EditIcon from '@mui/icons-material/Edit';
import { Editor, EditorState, convertToRaw } from 'draft-js';
import Box from '@mui/material/Box';
import useStory from '../../hooks/story';
import './memory.css';

export default function MemoryStory({ editable, prevEditorState, handleEditorStateUpdate }) {
  const [edit, setEdit] = useState(editable);
  const [startEdit, setStartEdit] = useState(false);
  const [editorState, setEditorState, handleEditorStateChange] = useStory(prevEditorState);
  const storyRef = useRef();

  useEffect(() => {
    setEdit(editable);
    setEditorState(prevEditorState);
    setStartEdit(false);
  }, [editable, prevEditorState]);
  console.log('Memory Title ', edit, startEdit);

  if (!edit && !startEdit) {
    return (
      <Editor
        readOnly
        editorState={editorState}
      />
    );
  }

  if (edit && !startEdit) {
    return (
      <Stack direction="column" sx={{ alignItems: 'flex-end', justifyContent: 'flex-end' }} spacing={2}>
        <EditIcon
          fontSize="medium"
          onClick={() => {
            setStartEdit(true);
          }}
        />
        <Editor
          readOnly
          editorState={editorState}
        />
      </Stack>
    );
  }

  if (edit && startEdit) {
    return (
      <Stack direction="column" sx={{ alignItems: 'flex-end', justifyContent: 'flex-start' }} spacing={2}>
        <Stack direction="row" spacing={2}>
          <DoneTwoToneIcon
            fontSize="medium"
            onClick={() => {
              handleEditorStateUpdate(
                JSON.stringify(convertToRaw(editorState.getCurrentContent())),
              );
              setStartEdit(false);
            }}
          />
          <ClearIcon
            fontSize="medium"
            onClick={() => {
              setEditorState(prevEditorState);
              setStartEdit(false);
            }}
          />
        </Stack>
        <Box
          sx={{
            p: 2,
            m: 1,
            my: 2,
            border: 1,
            borderColor: 'primary.main',
            bgcolor: 'input.background',
            minHeight: '200px',
            borderRadius: 5,
            width: '90%',
          }}
          onClick={() => storyRef.current.focus()}
        >
          <Editor
            autoFocus
            onChange={handleEditorStateChange}
            editorState={editorState}
            ref={storyRef}
          />
        </Box>
      </Stack>
    );
  }
}

MemoryStory.propTypes = {
  prevEditorState: PropTypes.instanceOf(EditorState).isRequired,
  editable: PropTypes.bool.isRequired,
  handleEditorStateUpdate: PropTypes.func.isRequired,
};
