import { useParams, Navigate } from 'react-router-dom';
import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import { EditorState, convertFromRaw } from 'draft-js';

import api from '../Api';
import BasePage from '../base_page';
import MemoryContent from './MemoryContent';
import MemoryContentLoading from './MemoryContentLoading';
import SimpleSnackbar from '../../components/simple_snackbar';

async function getMemory(
  bucket,
  authURL,
  memoryId,
  handleMemoryCallback,
  handleMemoryError,
) {
  try {
    const memoryData = await api.viewMemory(bucket, authURL, memoryId);
    console.log(memoryData);
    if (memoryData.status) {
      handleMemoryError(memoryData);
      return;
    }
    handleMemoryCallback(memoryData);
  } catch (error) {
    console.log(error);
  }
}

export default function ViewMemory({ bucket, authURL }) {
  const params = useParams();
  const { memoryId } = params;
  const [memory, setMemory] = useState();
  const [snack, setSnack] = useState(null);
  const [snackMessage, setSnackMessage] = useState(null);

  const handleMemory = (mem) => setMemory(mem);

  const [memError, setMemError] = useState(null);

  const handleSnackMessage = (msg) => {
    console.log('Setting snack message', msg);
    setSnackMessage(msg);
  };

  const handleSnackBarClose = () => {
    console.log('snack close, set snack false, message null');
    setSnack(false);
    setSnackMessage(null);
  };

  const handleMemoryError = (results) => {
    if (results.status === 'unauthenticated') {
      handleSnackMessage('You have been logged out. Navigating to login page');
      setSnack(true);
      setInterval(() => setMemError('unauthenticated'), 3000);
    }

    if (results.status === 'forbidden') {
      handleSnackMessage('You do not have access to this memory. Navigating to memories page');
      setSnack(true);
      setInterval(() => setMemError('forbidden'), 3000);
    }
  };

  useEffect(() => {
    getMemory(bucket, authURL, memoryId, handleMemory, handleMemoryError);
    const interval = setInterval(
      () => getMemory(bucket, authURL, memoryId, handleMemory, handleMemoryError),
      300000,
    );
    return () => clearInterval(interval);
  }, []);

  if (memError === 'unauthenticated') {
    return <Navigate to="/login" />;
  }

  if (memError === 'forbidden') {
    return <Navigate to="/memories" />;
  }

  console.log('memory', memory, 'memError', memError);
  return memory ? (
    <BasePage>
      <MemoryContent
        memTitle={memory.title}
        memDate={memory.date}
        memImages={memory.pictures.photos}
        memEditorState={EditorState.createWithContent(convertFromRaw(memory.contentState))}
        memoryID={memoryId}
        bucket={bucket}
        authURL={authURL}
        preview={false}
      />
      <SimpleSnackbar
        snackBarOpen={snack}
        message={snackMessage}
        onClose={handleSnackBarClose}
      />
    </BasePage>
  ) : (
    <BasePage>
      <MemoryContentLoading />
      <SimpleSnackbar
        snackBarOpen={snack}
        message={snackMessage}
        onClose={handleSnackBarClose}
      />
    </BasePage>
  );
}

ViewMemory.propTypes = {
  bucket: PropTypes.string.isRequired,
  authURL: PropTypes.string.isRequired,
};
