import * as React from 'react';
import PropTypes from 'prop-types';
import SwipeableViews from 'react-swipeable-views';
import Box from '@mui/material/Box';
import { autoPlay } from 'react-swipeable-views-utils';

const AutoPlaySwipeableViews = autoPlay(SwipeableViews);

export default function MemoryImageSlide({ index, images, handleNext }) {
  const [activeStep, setActiveStep] = React.useState(index);

  React.useEffect(() => {
    setActiveStep(index);
  }, [index]);

  const handleStepChange = (step) => {
    setActiveStep(step);
  };

  const imgElements = images.map((image) => {
    const srcSetList = image.images ? image.images.map(({ src, width }) => `${src} ${width}w`) : [];
    const srcSet = srcSetList.join(',');
    const style = {
      display: 'block',
      boxSizing: 'content-box',
      cursor: 'pointer',
      objectFit: 'contain',
      height: '100vh',
    };
    return (
      <Box
        component="img"
        sx={{
          display: 'flex',
          justifyContents: 'center',
          width: '100%',
          bgcolor: '#1c1c1c',
        }}
        src={image.src}
        srcSet={srcSet}
        style={style}
        alt={image.src}
        key={image.src}
      />
    );
  });

  return (
    <AutoPlaySwipeableViews
      index={activeStep}
      onChangeIndex={handleStepChange}
      enableMouseEvents
      animateTransitions
      interval={10000}
      onClick={handleNext}
      springConfig={{
        duration: '0.7s',
        easeFunction: 'cubic-bezier(0.83, 0, 0.17, 1)',
        delay: '0s',
      }}
      style={{ backgroundColor: 'muted.main' }}
    >
      {imgElements}
    </AutoPlaySwipeableViews>
  );
}

MemoryImageSlide.defaultProps = {
  index: 0,
};

MemoryImageSlide.propTypes = {
  index: PropTypes.number,
  handleNext: PropTypes.func.isRequired,
  images: PropTypes.arrayOf(PropTypes.shape({
    src: PropTypes.string,
    width: PropTypes.number,
    height: PropTypes.number,
  })).isRequired,
};
