import { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Snackbar from '@mui/material/Snackbar';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';

export default function SimpleSnackbar({ snackBarOpen, message, onClose }) {
  const [open, setOpen] = useState(snackBarOpen);
  const [snackMessage, setSnackMessage] = useState(message);

  useEffect(() => setOpen(snackBarOpen), [snackBarOpen]);
  useEffect(() => setSnackMessage(message), [message]);

  const handleClose = () => {
    /*    if (reason === 'clickaway') { */
    /* return; */
    /* } */

    setOpen(false);
    setSnackMessage(null);
    onClose();
  };

  const action = (
    <IconButton
      size="small"
      aria-label="close"
      color="inherit"
      onClick={handleClose}
    >
      <CloseIcon fontSize="small" />
    </IconButton>
  );

  console.log('in snackbar', open, message);

  return (
    <Snackbar
      open={open}
      autoHideDuration={3000}
      onClose={handleClose}
      message={snackMessage}
      action={action}
      anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
    />
  );
}

SimpleSnackbar.propTypes = {
  message: PropTypes.string.isRequired,
  snackBarOpen: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
};
