import PropTypes from 'prop-types';
//  import PhotoAlbum from 'react-photo-album';
import { useState, useEffect } from 'react';
import { EditorState } from 'draft-js';
import Button from '@mui/material/Button';
import DoneIcon from '@mui/icons-material/Done';
import Typography from '@mui/material/Typography';
import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import MemoryCarousel from './MemoryCarousel';
import MemoryContentStructure from './MemoryContentStructure';
import MemoryTitle from './MemoryTitle';
import MemoryDate from './MemoryDate';
import MemoryStory from './MemoryStory';
import MemoryImages from './MemoryImages';
import api from '../Api';
import SimpleSnackbar from '../../components/simple_snackbar';
import './memory.css';

export default function MemoryContent({
  memTitle,
  memDate,
  memEditorState,
  memImages,
  bucket,
  memoryID,
  authURL,
  preview,
}) {
  const [open, setOpen] = useState(false);
  const [edit, setEdit] = useState(false);
  const [activeIndex, setActiveIndex] = useState(0);
  const [title, setTitle] = useState(memTitle);
  const [date, setDate] = useState(memDate);
  const [editorState, setEditorState] = useState(memEditorState);
  const [images, setImages] = useState(memImages);
  const [snack, setSnack] = useState(null);
  const [snackMessage, setSnackMessage] = useState(null);

  console.log('images', images);
  const maxSteps = images.length;

  useEffect(() => setTitle(memTitle), [memTitle]);
  useEffect(() => setDate(memDate), [memDate]);
  useEffect(() => setEditorState(memEditorState), [memEditorState]);
  useEffect(() => setImages(memImages), [memImages]);

  const handleSnackMessage = (msg) => {
    console.log('Setting snack message', msg);
    setSnackMessage(msg);
  };
  const handleSnackBarClose = () => {
    console.log('snack close, set snack false, message null');
    setSnack(false);
    setSnackMessage(null);
  };

  const UpdateDetails = async (item, value) => {
    try {
      const resp = await api.updateMemoryDetails(
        bucket,
        authURL,
        memoryID,
        item,
        value,
      );
      if (resp) {
        handleSnackMessage(`Memory ${item} updated successfully`);
      } else {
        handleSnackMessage(`Error while updating memory ${item}`);
      }
      setSnack(true);
      return resp;
    } catch (error) {
      console.log(error);
      return false;
    }
  };

  const UpdateThumbnail = async (image) => {
    try {
      const resp = await api.updateMemoryImage(
        bucket,
        authURL,
        memoryID,
        'thumbnail',
        image,
      );
      console.log('Response', resp);
      if (resp.ok) {
        handleSnackMessage('Memory thumbnail updated successfully');
      } else {
        handleSnackMessage('Error while updating memory thumbnail');
      }
      setSnack(true);
      return resp;
    } catch (error) {
      console.log(error);
      return false;
    }
  };

  const deleteImage = async (image) => {
    try {
      let resp;
      if (images.length > 1) {
        resp = await api.updateMemoryImage(
          bucket,
          authURL,
          memoryID,
          'delete',
          image,
        );
        console.log('Response', resp);
        if (resp) {
          console.log('set snack message - successfully deleted memory image');
          handleSnackMessage('Memory image deleted successfully');
          setImages(images.filter((item) => item.title !== image.title));
        } else {
          console.log('set snack message - error while deleting memory image');
          handleSnackMessage('Error while deleting memory image');
        }
      } else {
        console.log('set snack message - atleast one image is needed for memory');
        handleSnackMessage('Atleast 1 image is needed for memory. Unable to delete');
      }
      setSnack(true);
      return resp;
    } catch (error) {
      console.log(error);
      return false;
    }
  };

  const addImage = async (details) => {
    try {
      console.log('addImage details', details);
      const resp = await api.updateMemoryImage(
        bucket,
        authURL,
        memoryID,
        'add',
        details,
      );
      return resp;
    } catch (error) {
      console.log(error);
      return false;
    }
  };

  const handleImageAdd = async (event) => {
    if (event.target.files) {
      const files = Array.from(event.target.files);
      console.log('files to add:', files.length, 'exsting images:', images.length);
      if (images.length + files.length <= 15) {
        files.forEach((file) => {
          const image = new Image();
          image.onload = async (e) => {
            const height = e.target.naturalHeight;
            const width = e.target.naturalWidth;
            const size = file.size / (1024 * 1024);
            const { name } = file;
            const resImages = [500, 1000, 1500].map((resHeight) => ({
              height: resHeight, width: Math.ceil((resHeight * width) / height),
            }));
            const creationDate = Date.now();
            const newName = `${creationDate}-${name}`;
            const imageDetails = {
              name: newName, size, height, width, images: resImages,
            };
            const resp = await addImage(imageDetails);
            if (resp.ok) {
              const photo = {
                key: `memories/${memoryID}/images/${newName}`,
                src: e.target.src,
                alt: newName,
                title: newName,
                width,
                height,
                images: [],
              };
              console.log('uploading photo', photo);
              const putURL = await resp.text();
              handleSnackMessage('Uploading image. Please wait...');
              setSnack(true);
              const uploadResp = await fetch(putURL, { body: file, method: 'PUT' }).then((response) => response);
              if (uploadResp.ok) {
                setImages((pics) => pics.concat([photo]));
                console.log('Setting snack message - image add success');
                handleSnackMessage('Memory image added successfully');
                setSnack(true);
                return;
              }
              console.log('Setting snack message - error uploading image');
              handleSnackMessage('Error while uploading memory image');
              setSnack(true);
              return;
            }
            console.log('Setting snack message - error adding image');
            handleSnackMessage('Error while adding memory image');
            setSnack(true);
          };
          image.src = URL.createObjectURL(file);
        });
        files.map((file) => URL.revokeObjectURL(file)); // avoid memory leak
      } else {
        console.log('Setting snack message - error adding image as exceeding 15');
        handleSnackMessage(`Unable to add ${files.length} images. Maximum images allowed pre memory is 15`);
        setSnack(true);
      }
      //  console.log('set snack true, handleImageAdd');
    }
  };

  const updateTitle = (value) => {
    const resp = UpdateDetails('title', value);
    if (resp) {
      setTitle(value);
    }
  };

  const updateDate = (value) => {
    const resp = UpdateDetails('date', value);
    if (resp) {
      setDate(value);
    }
  };

  const updateEditorState = (value) => {
    const resp = UpdateDetails('story', value);
    if (resp) {
      setEditorState(value);
    }
  };

  const handleClose = () => setOpen(false);

  const handleBack = () => {
    setActiveIndex(
      (prevActiveStep) => (prevActiveStep - 1 + maxSteps) % maxSteps,
    );
  };

  const handleNext = () => {
    setActiveIndex((prevActiveStep) => (prevActiveStep + 1) % maxSteps);
  };

  const handlePhotoClick = (photo, event, index) => {
    setOpen(true);
    setActiveIndex(index);
  };

  const handleEditClick = () => setEdit(true);

  const memoryTitle = (
    <MemoryTitle
      editable={edit}
      text={title}
      handleTitleUpdate={updateTitle}
    />
  );

  const memoryDate = (
    <MemoryDate editable={edit} prevDate={date} handleDateUpdate={updateDate} />
  );

  const storyLayout = (
    <MemoryStory
      editable={edit}
      prevEditorState={editorState}
      handleEditorStateUpdate={updateEditorState}
    />
  );

  const photoLayout = (
    <MemoryImages
      editable={edit}
      images={images}
      handlePhotoClick={handlePhotoClick}
      handleThumbnailUpdate={UpdateThumbnail}
      deleteImage={deleteImage}
      addImage={handleImageAdd}
    />
  );

  const updateDone = (
    <Button
      variant="contained"
      component="label"
      onClick={() => setEdit(false)}
      sx={{
        color: 'primary.main',
        borderRadius: 5,
        backgroundColor: 'button.main',
        padding: '6px 8px',
      }}
    >
      <DoneIcon size="medium" sx={{ paddingRight: 1 }} />
      <Typography variant="body1" color="muted.main">Edit</Typography>
    </Button>
  );

  return (
    <>
      {edit ? (
        <Stack
          direction="row"
          justifyContent="flex-start"
          sx={{ backgroundColor: 'button.main', paddingX: 2, paddingY: 3 }}
        >
          {updateDone}
        </Stack>
      ) : null}
      <Box
        component="div"
        sx={{
          backgroundColor: 'button.main',
          margin: edit ? '5px' : 0,
        }}
      >
        <MemoryContentStructure
          header={memoryTitle}
          subheader={memoryDate}
          photoLayout={photoLayout}
          storyLayout={storyLayout}
          memoryID={memoryID}
          bucket={bucket}
          authURL={authURL}
          handleEditClick={handleEditClick}
          preview={preview}
        />
      </Box>
      <MemoryCarousel
        open={open}
        title={title}
        date={date}
        images={images}
        index={activeIndex}
        handleNext={handleNext}
        handleBack={handleBack}
        handleClose={handleClose}
      />
      <SimpleSnackbar
        snackBarOpen={snack}
        message={snackMessage}
        onClose={handleSnackBarClose}
      />
    </>
  );
}

MemoryContent.defaultProps = {
  memTitle: 'Memory Title',
  memDate: 'Memory Date',
  memEditorState: EditorState.createWithText('Memory Text'),
};

MemoryContent.propTypes = {
  memTitle: PropTypes.string,
  memDate: PropTypes.string,
  memEditorState: PropTypes.instanceOf(EditorState),
  memoryID: PropTypes.string.isRequired,
  bucket: PropTypes.string.isRequired,
  authURL: PropTypes.string.isRequired,
  memImages: PropTypes.arrayOf(
    PropTypes.shape({
      src: PropTypes.string,
      width: PropTypes.number,
      height: PropTypes.number,
    }),
  ).isRequired,
  preview: PropTypes.bool.isRequired,
};
